.productlist_img {
  width: 80px;
  height: 100px;
  object-fit: contain;
}
.p-img {
  height: 100%;
  width: 100%;
}

.categoryHeader {
  margin-left: 15em;
}
.select {
  width: 10rem;
  background-color: rgb(5, 153, 17);
  height: 3rem;
  border-radius: 0.5rem;
  text-decoration: blue;
}
.header {
  text-decoration: aquamarine;
}
.addproductcontrols {
  width: 100%;
  padding: 30px;
  display: flex;
  justify-content: center;
  flex-direction: column;
  position: relative;
}
.addproductcontrols button {
  outline: none;
  border: none;
  border-radius: 50%;
}
.btntop {
  width: 70%;
  position: absolute;
  top: 90px;
  display: flex;
  justify-content: space-between;
}
.btntop .MuiSvgIcon-root {
  border-radius: 50%;
  color: rgb(96, 156, 224);
  font-size: 30px;
}
.btnadd {
  display: flex;
  justify-content: center;
  align-items: center;
}
.btnadd button {
  background: none;
}
.btnadd .MuiSvgIcon-root {
  color: green;
  font-size: 50px;
}
.animation {
  animation: hide 300ms ease;
}
@keyframes hide {
  100% {
    margin-left: -400px;
    opacity: 0;
  }
}
.animationShow {
  animation: show 300ms ease;
}
@keyframes show {
  100% {
    margin-left: 400px;
    opacity: 0;
  }
}
.categories_addnew {
  background: rgb(95, 170, 240) !important;
  color: rgb(1, 38, 43);
  font-size: larger;
  font-weight: 900;
  padding-left: 90px;
  border-radius: 400px;
}
.update .modal-dialog {
  max-width: 83% !important;
  height: auto;
  margin: 20px 0px 0px auto;
}
.back {
  display: inline !important;
}
.w-50 {
  width: 50px !important;
  margin: 5px;
}
.center {
  text-align: center;
}
.delete_red_color {
  color: #cb3534;
}
.attribute-top,
.attribute-form {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.attribute-top:nth-child(2) {
  background-color: red;
}
